<template>
    <div class="my">
        <div class="bg-box">
            <img  :src="userData.userBg">
            <div class="upload-bg">
                <van-uploader :after-read="afterReadBg" />
            </div>
        </div>
        <div class="userdata-box">
            <div class="header-box">
                <div class="img-box">
                    <img :src="userData.userImg">
                    <div class="upload-img">
                        <van-uploader :after-read="afterReadHeader" />
                    </div>
                </div>
                <div class="header-text">
                    <div class="user-name">{{userData.nickName}}</div>
                    <div class="user-desc">{{userData.desc?userData.desc:'这个人很懒，什么也没有留下！'}}</div>
                </div>
            </div>
            <div class="data-box">
                <van-cell is-link to='/userdata'>
                    <template #title>
                        <span class="custom-title">个人资料</span>
                    </template>
                </van-cell>
                <van-cell is-link to="/myorders">
                    <template #title>
                        <i :class="isNewOrderMsg?'myorder-icon':''"></i>
                        <span class="custom-title">我的订单</span>
                    </template>
                </van-cell>
                <van-cell is-link to="/mylike">
                    <template #title>
                        <i :class="isNewLike?'mylike-icon':''"></i>
                        <span class="custom-title">我的收藏</span>
                    </template>
                </van-cell>
                <van-cell is-link to="/addresslist">
                    <template #title>
                        <span class="custom-title">地址管理</span>
                    </template>
                </van-cell>
                <van-cell is-link to="/safecenter">
                    <template #title>
                        <span class="custom-title">安全中心</span>
                    </template>
                </van-cell>
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/less/my.less'
    import {mapState,mapMutations} from 'vuex'
    export default {
        name:"My",
        data(){
            return {
                
            }
        },
        methods:{
            getUserData(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findMy',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == "A001"){
                        this.changeUserData({...this.userData,...res.data.result[0]});
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //上传头像
            afterReadHeader(file){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                let limitType = ['png','gif','jpg','jpeg'];
                let uploadType = file.file.type.split('/')[1];
                if(limitType.indexOf(uploadType) == -1){
                    return this.$toast({
                        message:`文件类型只支持${limitType.join(",")}`,
                        forbidClick:true,
                        duration:1000
                    })
                }
                //获取base64编码
                let base = file.content.replace(/data:image\/[A-Za-z]+;base64,/,"");
                //加载提示
                this.$toast.loading({
                    message: '上传中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/updateAvatar',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        imgType:uploadType,
                        serverBase64Img:base
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == "H001"){
                        if(res.data.result[0] == 1){
                            this.changeUserHeader(res.data.userImg);
                        }
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //上传背景图
            afterReadBg(file){
              let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                let limitType = ['png','gif','jpg','jpeg'];
                let uploadType = file.file.type.split('/')[1];
                if(limitType.indexOf(uploadType) == -1){
                    return this.$toast({
                        message:`文件类型只支持${limitType.join(",")}`,
                        forbidClick:true,
                        duration:1000
                    })
                }
                //获取base64编码
                let base = file.content.replace(/data:image\/[A-Za-z]+;base64,/,"");
                //加载提示
                this.$toast.loading({
                    message: '上传中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/updateUserBg',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        imgType:uploadType,
                        serverBase64Img:base
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == "I001"){
                        if(res.data.result[0] == 1){
                            this.changeUserBg(res.data.userBg);
                        }
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });  
            },
            ...mapMutations(["changeUserData","changeUserHeader","changeUserBg"])
        },
        created(){
            this.getUserData();
        },
        computed:{
            ...mapState(["userData","isNewOrderMsg","isNewLike"])
        }
    }
</script>

